@import url(https://fonts.googleapis.com/css2?family=Pinyon+Script&display=swap);
body {
  margin: 0;
  background-image: url(https://i.pinimg.com/564x/45/28/6e/45286e6cc0c05603623cc060adb04e1a.jpg);
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.card {
  margin: 10px;
  background-color: rgba(229, 255, 0, 0.15);
  border: 2px solid rgb(7, 61, 7);
  border-radius: 5px;
  width: 350px;

  font-family: "Pinyon Script", cursive;
  font-size: 20px;
}

h2,
h3 {
  margin: 10px;
}

span {
  color: #ed3330;
}

.error {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 10px;
  width: 350px;
  height: 80px;

  background-color: #ed3330a6;
  border: 1px solid black;
  border-radius: 5px;
  font-size: 20px;
}

.hidden {
  display: none;
}

.inputs {
  float: left;
}

.user-select {
  margin-top: 5px;
  height: 30px;
  font-size: 20px;
}

.title {
  font-size: 30px;
}

#TODO-adder {
  height: 30px;
  width: 230px;
  font-size: 20px;
}

.add {
  width: 90px;
  padding: 27px;
  margin: 34px 0 0 20px;

  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  background: #ed3330;
  border-radius: 5px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

#add:hover {
  background: #434343;
  box-shadow: 5px 40px -10px rgba(0,0,0,0.57);
  transition: all 0.4s ease 0s;
}

